@import "./reset/reset.css";
@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;600;700&display=swap');
$green: #8cbf41;
@mixin flex-jc {
    display: flex;
    justify-content: center; }
@mixin flex-ac {
    display: flex;
    align-items: center; }
@mixin flex {
    display: flex;
    align-items: center;
    justify-content: center; }
@mixin bg {
    background-position: center;
    background-repeat: no-repeat;
    display: inline-block; }
@mixin wh($width, $height) {
    width: $width;
    height: $height; }
@mixin text-settings($fstyle, $fweight, $fsize, $color) {
    font-style: $fstyle;
    font-weight: $fweight;
    font-size: $fsize;
    color: $color;
    font-family: 'Lora', serif; }
@mixin border {
    transform: skew(-25deg); }
@mixin link($color) {
    text-decoration: none;
    text-transform: none;
    outline: none;
    color: $color; }
@mixin h3($color) {
    padding-top: 40px;
    padding-left: 30px;
    @include text-settings(normal, 700, 25px, $color);
    text-shadow: 0 -1px 0 #fff, 0 1px 0 #2e2e2e, 0 2px 0 #2c2c2c, 0 3px 0 #2a2a2a; }
html {
    box-sizing: border-box; }
*, *:before, *:after {
    box-sizing: inherit; }
body {
    overflow-x: hidden;
    font-style: normal; }
.wrapper {
    margin: 0;
    min-height: 500px;
    @include flex-ac;
    flex-direction: column; }
.inline-wrapper {
    @include flex-jc;
    width: 1440px;
    z-index: 1; }
.wrapper, header, main, .inline-header, .col-h50, .defaultPage {
    width: 100%; }
a {
    text-decoration: none;
    outline: none; }
.defaultPage {
    @include flex-ac;
    flex-direction: column; }
.col-w8 {
    width: 8%;
    min-width: 113px; }
.col-w19 {
    width: 19%; }
.col-w20 {
    width: 20%; }
.col-w40 {
    width: 40%; }
.col-w50 {
    width: 50%; }
.col-w60 {
    width: 60%; }
.col-w80 {
    width: 80%; }
.col-w92 {
    width: 92%; }
.col-w100 {
    width: 100%; }
.col-h50 {
    display: block;
    height: 50%; }
.col-h30 {
    height: 30%; }
.col-h35 {
    height: 35%; }
.col-h40 {
    height: 40%; }
.col-h70 {
    height: 70%; }
.h100 {
    height: 100%; }
@import "../arrowTop/arrowTop.sass";
@import "../header/header.sass";
@import "../main/main.sass";
@import "../main/slider/slider.sass";
@import "../main/services/services.sass";
@import "../main/ourAdvantages/ourAdvantages.sass";
@import "../main/howItWork/howItWork.sass";
@import "../main/job/job.sass";
@import "../main/partners/partners.sass";
@import "../footer/footer.sass";
@import "../main/geography/geography.sass";
@import "../main/transport/transport.sass";
@media screen and (max-width: 1399px) {
    .inline-wrapper {
        width: 100%; } }
